:root {
  font-size: 62.5%;
}
body {
  font-family: "Open Sans", sans-serif;
}
h1 {
  font-size: 3.5rem;
}
p {
  font-size: 1.5rem;
}
h2 {
  font-size: 2.7rem;
}
li a {
  text-decoration: none;
  color: #2d2f31;
  width: 100%;
}
li:hover {
  background-color: black;
}
nav {
  width: 100%;
  background: #d9d9d9;
  margin: 4rem auto;
}
span {
  padding: 3rem;
  background: #2d2f31;
  color: white;
  font-size: 2.5rem;
  cursor: pointer;
  display: block;
}

span::after {
  float: right;
  right: 10%;
  content: "+";
}

.slide {
  clear: both;
  width: 100%;
  height: 0px;
  overflow: hidden;
  transition: height 0.4s ease;
}
.slide li {
  padding: 3rem;
}

#touch {
  position: absolute;
  opacity: 0;
  height: 0px;
}

#touch:checked + .slide {
  height: 30rem;
}
